// This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with
// each doing the same thing just structuring the functions/data differently.
app.service('trackingService', ['$q', 'applicationLoggingService', 'configurationService', 'dataService', 'loggedInUser',
  function ($q, logger, configurationService, dataService, $loggedInUser) {
    var trackingService = this

    $q.all([dataService.getAppProperties(), dataService.getServerInfo(), $loggedInUser]).then(values => {
      const appProperties = values[0]
      const serverInfo = values[1]
      const ua = new UAParser(navigator.userAgent)
      const tenantName = serverInfo.baseUrl.match(/https:\/\/(.+?)\./)[1]
      Avo.initAvo(
        {
          env: ['timereports', 'avelytchenko'].indexOf(tenantName) >= 0 ? 'dev' : 'prod',
          webDebugger: false,
          strict: false
        },
        {
          appVersion: appProperties.version || '1',
          instanceUuid: serverInfo.scmInfo,
          baseUrl: serverInfo.baseUrl,
          jiraSen: appProperties.supportEntitlementNumber,
          webBrowser: ua.getBrowser().name || 'unknown',
          webBrowserVersion: ua.getBrowser().version || 'unknown',
          operatingSystem: ua.getOS().name || 'unknown',
          operatingSystemVersion: ua.getOS().version || 'unknown'
        }
      )
      var user = values[2]
      configurationService.getUserConfiguration(user).then(userPreferences => {
        trackingService.track('identifiedUser', {
          userId_: user.accountId,
          id: user.accountId,
          userTimezone: user.timeZone,
          userContinent: user.timeZone && user.timeZone.split('/')[0],
          userLocale: user.locale,
          jiraAdministrator: user.isAdministrator,
          weekendType: userPreferences.weekendType.val,
          reportingDayDefaultValue: userPreferences.reportingDay.val,
          durationTypeDefaultValue: userPreferences.durationType.val,
          maximumFractionDigits: userPreferences.maxFractionDigits.val,
          durationTypeForExport: userPreferences.durationTypeForExport.val,
          startedTimeInStatus: userPreferences.startedTimeInStatus.val,
          inProgressIssuesJql: userPreferences.inProgressIssuesJql.val,
          showDetailsCount: userPreferences.showDetailsCount.val,
          showCategoryRow: userPreferences.showCategoryRow.val,
          showTotalInFront: userPreferences.showTotalInFront.val,
          tempoToken: userPreferences.tempoToken.val,
          workingTimeInStatusFrom: userPreferences.workingTimeInStatus.val && userPreferences.workingTimeInStatus.val.from,
          workingTimeInStatusTo: userPreferences.workingTimeInStatus.val && userPreferences.workingTimeInStatus.val.to
        })
      })
    })

    this.track = (event, data) => {
      if (Avo[event]) {
        try {
          Avo[event](data)
        } catch (err) {
          logger.error('Avo Error', err)
        }
      } else {
        logger.debug('Unsupported event', event)
      }
    }

    // TODO: event methods
  }
])
